@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Nunito", Helvetica, Tahoma, Arial, sans-serif;
  font-size: 16px;
  background: #e2e8f0;
}
.home{display:flex;flex-direction:column;align-items:center;height:100vh}.home-title{font-size:60px;margin-top:12px;color:#2c5282;font-weight:600;border-bottom:2px #4299e1 solid}.form-flexbox{height:100%;width:60%;display:flex;align-items:center;justify-content:space-between}.form-flexbox .form-container{padding:32px}.vertical-divider{border-left:2px #2078ca solid;height:600px}

.form-title{font-family:'Roboto', serif;font-weight:400;font-size:32px;color:#2d3748;margin-bottom:8px}.form-subtitle{font-size:20px;color:#4a5568;margin-bottom:36px}.sign-up-form{border-radius:16px;background:white;min-width:400px;width:90%;max-width:600px;box-shadow:0 10px 15px -3px rgba(0,0,0,0.1),0 4px 6px -2px rgba(0,0,0,0.05)}.sign-up-form .submit-btn{color:white;cursor:pointer;display:block;width:100%;height:60px;border:none;outline:none;background:#2078ca;font-family:"Nunito",Helvetica,Tahoma,Arial,sans-serif;font-size:16px;font-weight:500;text-transform:uppercase;border-bottom-left-radius:16px;border-bottom-right-radius:16px}.sign-up-form .submit-btn:active{background:#2b6cb0}.form-fields{padding:24px;display:grid}.input-wrapper{position:relative;width:100%;color:#718096;background:#e2e8f0;border:2px #e2e8f0 solid;border-radius:5px;transition:background .2s ease-out;margin-bottom:16px}.input-wrapper:focus-within{background:white;border:2px #e2e8f0 solid}.input-wrapper.required{box-shadow:0 0 1px 2px #f6ad55;background:white;border:2px white solid}.input-img-wrapper{display:flex;align-items:center;margin:0 12px 0 12px;height:100%;position:absolute;top:0;color:#a0aec0}input[type=text],input[type=email],input[type=password],select{height:50px;width:100%;font-family:"Nunito",Helvetica,Tahoma,Arial,sans-serif;font-size:16px;font-weight:500;background:none;outline:none;border:none;padding-left:44px}textarea{display:block;font-family:"Nunito",Helvetica,Tahoma,Arial,sans-serif;font-size:16px;font-weight:500;width:100%;border:none;background:none;outline:none;resize:none;padding:12px 12px 12px 44px;line-height:25px}.error{display:flex;align-items:center;color:rgba(237,137,54,0.7);font-size:16px;margin-bottom:16px}.hide{display:none}

