.user-home{display:flex;flex-direction:column;align-items:center;height:100vh}.user-home .title{font-size:60px;margin-top:12px;color:#2078ca;font-weight:600}.user-home .sub{font-size:30px;margin-top:8px;color:#2a4365}.user-home .primary-btn{margin-top:12px;font-family:'Roboto', serif;font-weight:500;font-size:20px;padding:8px 12px;background:#fc8181;color:#742a2a;border:none;border-radius:8px;cursor:pointer}.user-home .primary-btn:active{outline:none;background:#e53e3e}.user-home .primary-btn:focus{outline:none}

